.loader {
    width: 100%;
    height: 4px;
    position: absolute;
    left: 0;
    overflow: hidden;
  
    &::after{
    content: "";
    width: 192px;
    height: 4px;
    background: var(--purple);
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    animation: animloader 2s linear infinite;
    }
  }
  
  @keyframes animloader {
    0% {
        left: 0;
        transform: translateX(-100%);
    }
    100% {
        left: 100%;
        transform: translateX(0%);
    }
  }